var debounce = require("lodash/debounce");

export function ExtensionsListSearchBox(id, options) {
  this._options = options;
  this._searchFunction = this._options.searchFunction || function () {};
  this._displayAll = this._options.displayAll || function () {};

  var _document = this.getDocument();
  this.container = _document.createElement("div");
  this.container.id = id;
  this.container.classList.add("adsk-control");
  this.container.classList.add("adsk-searchbox");
  this.container.classList.add("empty");

  var searchbox = _document.createElement("input");
  searchbox.classList.add("search-box");
  searchbox.classList.add("docking-panel-delimiter-shadow");
  searchbox.type = "search";
  searchbox.results = [];

  searchbox.addEventListener("input", function () {
    checkAndSearch(this); // delayed: as typing
  });

  searchbox.placeholder = Autodesk.Viewing.i18n.translate("Search");
  searchbox.setAttribute("data-i18n", "Search");

  this.container.appendChild(searchbox);
  this.searchbox = searchbox;

  var searchboxIcon = document.createElement("div");
  searchboxIcon.className = "search-box-icon";
  this.container.insertBefore(searchboxIcon, searchbox.nextSibling);

  var searchboxClose = document.createElement("div");
  searchboxClose.className = "search-box-close";
  searchboxClose.addEventListener("click", function () {
    clearSearchBox();
  });
  this.container.appendChild(searchboxClose);

  var noResultDiv = _document.createElement("div");
  noResultDiv.classList.add("no-results-container");
  noResultDiv.innerText = Autodesk.Viewing.i18n.translate("No results found");
  noResultDiv.style.display = "none";
  this.container.appendChild(noResultDiv);
  this.noResultDiv = noResultDiv;

  function clearSearchBox() {
    self.searchbox.value = "";
    self.container.classList.add("empty");
    self.searchbox.classList.remove("searching");
    showNoResultsDiv(false);
    self._displayAll();
  }

  function showNoResultsDiv(display) {
    self.noResultDiv.style.display = display ? "" : "none";
  }

  var self = this;

  var delayFor = 800;
  var checkAndSearch = debounce(function () {
    if (searchbox.value.length === 0) {
      self.container.classList.add("empty");
      showNoResultsDiv(false);
      self._displayAll();
      return;
    }

    self.container.classList.remove("empty");

    // prevent search while typing text that is too short.
    if (searchbox.value.length < 3) {
      return;
    }

    self.results = self._searchFunction(searchbox.value);
    showNoResultsDiv(!self.results.length);
  }, delayFor);
}

Autodesk.Viewing.GlobalManagerMixin.call(ExtensionsListSearchBox.prototype);